.topic {
	position: relative;
	color: $white;
	background-image: linear-gradient(138deg, #2D3353 9%, #3E3E88 70%);
	&__bg {
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		background-position: 40% 21%;
		background-repeat: no-repeat; }
	&__left {
		position: absolute;
		top: 0;
		left: 0;
		bottom: 0;
		z-index: 2;
		padding: 260px 0 44px 40px;
		font-size: 12px;
		@include md {
			display: none; } }
	&__mail {
		position: absolute;
		left: 73px;
		bottom: 33px;
		transform: rotate(180deg);
		@include xl {
			left: 40px; } }
	&__inner {
		position: relative;
		padding: 170px 40px 135px 419px;
		max-width: 1680px;
		height: 100vh;
		min-height: 630px;
		@include vertical;
		text-align: left;
		@include xl {
			padding-left: 120px;
			text-align: center; }
		@include md {
			padding: 120px 20px; } }
	&__wrap {
		position: relative;
		width: 100%;
		max-width: 1078px;
		z-index: 1;
		text-align: left;
		vertical-align: middle;
		display: inline-block;
		@include md {
			max-width: 778px; }
		@include sm {
			max-width: 510px; } }
	&__footer {
		margin-top: 35px; }
	&__tooltip {
		position: absolute;
		right: 19px;
		bottom: 26px; } }

.mail {
	color: $white;
	font-size: 12px;
	font-weight: 400;
	writing-mode: vertical-lr;
	&:before {
		position: absolute;
		content: "";
		top: -5px;
		left: -5px;
		right: -5px;
		bottom: -5px; } }

.share {
	font-size: 15px;
	&__item {
		display: block;
		&:not(:last-child) {
			margin-bottom: 5px; } }
	a {
		position: relative;
		color: inherit;
		display: block;
		&:before {
			position: absolute;
			content: "";
			top: -4px;
			left: -5px;
			right: -5px;
			bottom: -4px; } } }
