.tooltip {
  position: relative;
  z-index: 99;
  padding: 8px 80px 8px 20px;
  color: rgba(255, 255, 255, 0.5);
  font-size: 16px;
  font-weight: 400;
  line-height: 1.5;
  letter-spacing: 0;
  width: 641px;
  text-align: left;
  border-radius: 4px;
  background-color: rgba(0, 0, 0, 0.5);
  display: inline-block;
}

@media only screen and (max-width: 767px) {
