.sticky-nav {
	position: absolute !important;
	top: 0;
	left: 0;
	width: 305px;
	@include md {
		display: none; }
	&__inner {
		position: relative;
		@include gpu; }
	&__container {
		padding: 36px 39px 43px;
		background: $lightGray; }
	&__label {
		margin-bottom: 22px; }
	&__link {
		color: #D4D4D4;
		@include f(28px, 700, 2);
		cursor: pointer;
		transition: color 0.25s;
		&.is-active {
			color: $violet; }
		@include hover {
			color: $violet; } }
	&__line {
		position: absolute;
		top: 0;
		right: 0;
		border-right: 5px solid $violet;
		height: 26px;
		opacity: 0;
		@include gpu;
		transition: opacity 0.25s, transform 0.25s;
		&[data-state="true"] {
			opacity: 1; } } }
